.calendar {
  min-width: 320px;
}

.month {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  height: 80px;
  text-transform: capitalize;
}

.row {
  display: flex;
  height: calc((100vh / 5) - 24px);
}

.row:nth-child(2) {
  height: 40px;
}

.day,
.date,
.emptyDate {
  display: flex;
  flex-direction: column;
  flex: 1;
  border: thin solid #eee;
}

.day {
  padding: 10px 0 10px 5px;
  height: 20px;
  font-weight: bold;
}

.date,
.emptyDate {
  padding: 5px;
  overflow: auto;
}

.today {
  background-color: #c2e5ff94;
  border: thin solid #c2e5ff94;
  font-size: 1.2rem;
}

.date:hover {
  background-color: whitesmoke;
  cursor: pointer;
}

.today:hover{
  background-color: #c2e5ffda;
}

.event{
  display: flex;
  overflow: auto;
  font-size: 0.8rem;
  border: thin solid #888;
  margin-top: 4px;
  padding: 2px;
  justify-content: space-between;
  background-color: cornsilk;
  min-height: 0.8rem;
}

.eventName{
  margin-left: 4px;
}