.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.65);
  z-index: 9;
}

.eventCreator {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: thin solid #eee;
  padding: 25px;
  width: 60%;
}

input,
label {
  display: block;
  margin: 12px 0;
  width: 100%;
  box-sizing: border-box;
}

input {
  padding: 8px;
  border: thin solid #e1e1e1;
}

button {
  padding: 8px;
  margin-top: 15px;
  float: right;
  cursor: pointer;
  border: thin solid #e1e1e1;
  background: white;
  margin-left: 10px;
}

button:focus {
  outline: none;
}

button:hover {
  background: teal;
  color: white;
  border: thin solid teal;
}
